import { stopSubmit } from "redux-form";
import UsersAPI from "../api/api";

const AUTH_USERS = "AUTH_USERS";
const CAPCHA = "CAPCHA";

type initialStateType = {
    userid: number,
    email: string,
    login: string,
    isAuth: boolean,
    capha: string | null,
    isCapha: boolean
}
let initialState: initialStateType = {
    userid: 0,
    email: "",
    login: "",
    isAuth: false,
    capha: null,
    isCapha: false
}
export const authReducer = (state: any = initialState, action: any) => {
    switch (action.type) {
        case AUTH_USERS:
        case CAPCHA:
            return {
                ...state,
                ...action.data
            }
        default:
            return state;
    }
}

export const authUsers = (userid: number, email: string, login: string, isAuth: boolean) => ({ type: AUTH_USERS, data: { userid, email, login, isAuth } });
const capchaAction = (capha: any) => ({ type: CAPCHA, data: { capha } });

export const authMeThunk = () => {
    return async (dispatch: any) => {
        const response = await UsersAPI.authMe();
        if (response.resultCode === 0) {
            let { id, email, login } = response.data;
            dispatch(authUsers(id, email, login, true));
        }
    }
}

export const logIn = (email: string, password: any, rememberMe: boolean = false, captcha: boolean = false) => async (dispatch: any) => {
    try {
        const response = await UsersAPI.Login(email, password, rememberMe, captcha);
        if (response.resultCode === 0) {
            dispatch(authMeThunk());
        } else {
            response.resultCode === 10 && dispatch(capcha());
            dispatch(stopSubmit('login', { _error: response.messages }));
        }
    } catch (error) {
        dispatch(capchaAction(null));
    }
}
export const logOut = () => {
    return async (dispatch: any) => {
        const response = await UsersAPI.LogOut();
        if (response.resultCode === 0) dispatch(authUsers(0, '', '', false));
    }
}

const capcha = () => async (dispatch: any) => {
    const response = await UsersAPI.capcha();
    dispatch(capchaAction(response.url));
}